import React, { useState } from "react";
import { ProductConsumer } from "../context/context";
import http from "../utils/httpService";
import CheckoutProduct from "./CheckoutProduct";
import Flip from "react-reveal/Flip";

import "./css/cart.css";
import MyModal from "./Modal";
import { getCoupon } from "./../utils/apiEndpoint";
import { Fade } from "react-bootstrap";

function ConfirmOrderCart({ hideCheckoutCart, setCoupon, addTotals }) {
  // modal Handlers
  const [openModal, setOpenModal] = useState(false);
  const handleOpen = () => {
    if (openModal === false) {
      setOpenModal(true);
    }
  };

  const handleClose = () => {
    if (openModal === true) {
      setOpenModal(false);
    }
  };

  const [couponcode, setcouponcode] = useState();
  const [couponMessage, setcouponMessage] = useState();

  const handleChangeCoupon = (e) => {
    setcouponcode(e.target.value);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const { data } = await http.get(getCoupon + "/" + couponcode);

    if (data.coupon !== null) {
      setcouponMessage("Coupon Applied Successfully");
      setCoupon(data.coupon);
      addTotals();
    } else {
      setcouponMessage("invalid Coupon Code");
      setCoupon({});
      addTotals();
    }
  };

  return (
    <div
      id="cart"
      className={
        hideCheckoutCart ? "checkOutBox px-2 active " : "checkOutBox px-2 "
      }
    >
      <ProductConsumer>
        {(value) => (
          <MyModal
            data={value}
            value={openModal}
            handleClose={handleClose}
            setDeliveryZone={value.setDeliveryZone}
            deliveryZones={value.deliveryZones}
          />
        )}
      </ProductConsumer>
      <h4 className="text-center pt-2">Cart</h4>
      {/* <!----Cart--> */}

      <ProductConsumer>
        {(value) => (
          <div className={value.cart.length === 0 ? "" : "scrollContainer"}>
            {value.cart.map((item) => (
              <CheckoutProduct item={item} value={value} key={item.id} />
            ))}
          </div>
        )}
      </ProductConsumer>

      {/* <!---Cart-->
    <!---checkOut--> */}

      <ProductConsumer>
        {(value) => (
          <div className="px-3 border-top pt-3">
            <div>
              <span className="">Delivery Area</span>{" "}
              <span
                className="float-right areaLink"
                onClick={() => handleOpen()}
              >
                {value.deliveryZone
                  ? value.deliveryZone.area_name
                  : "Please Select Area"}
              </span>
            </div>

            <div className="mt-4">
              <span className="">Subtotal</span>{" "}
              <span className="float-right">
                Rs<span className="subTotal">{value.cartSubtotal}</span>
              </span>
            </div>

            <div>
              <span className="">
                Gst(<span>16</span>%)
              </span>{" "}
              <span className="float-right">
                Rs<span className="gst">{value.cartTax}</span>
              </span>
            </div>

            <div>
              <span className="">Delivery Charges</span>{" "}
              <span className="float-right">
                Rs
                <span className="deliveryChrages">{value.deliveryCharge}</span>
              </span>
            </div>

            {Object.entries(value.coupon).length !== 0 && (
              <div className="mt-4">
                <span className="font-weight-bold">Discount</span>{" "}
                <span className="float-right">
                  <span className="totalPrice">
                    {value.coupon.discount_in_percentage}%
                  </span>
                </span>
              </div>
            )}

            <div className="mt-4">
              <span className="font-weight-bold">
                Total <sub>(incl. Gst)</sub>
              </span>{" "}
              <span className="float-right">
                Rs
                <span className="totalPrice">
                  {value.discountedTotal
                    ? value.discountedTotal
                    : value.cartTotal}
                </span>
              </span>
            </div>
            <form onSubmit={handleSubmit} className="couponForm">
              <div className="input-group  mt-3">
                <input
                  type="text"
                  className={
                    couponMessage === "invalid Coupon Code"
                      ? "form-control activeCoupon"
                      : "form-control"
                  }
                  placeholder="Add coupon here"
                  aria-label="Recipient's username"
                  aria-describedby="basic-addon2"
                  value={couponcode}
                  onChange={handleChangeCoupon}
                />

                <div className="input-group-append">
                  <button className="btn fntsz checkOutbtn" type="submit">
                    Apply
                  </button>
                </div>
              </div>
              {couponMessage && (
                <Flip top>
                  {" "}
                  <div
                    className={
                      couponMessage !== "invalid Coupon Code"
                        ? "errorDisp activeCouponMessage"
                        : "errorDisp"
                    }
                  >
                    {couponMessage}
                  </div>
                </Flip>
              )}
            </form>

            <div className="mt-3 text-center btnDiv">
              <button
                className="btn btn-block mb-2 checkOutbtn"
                form="checkoutForm"
              >
                {" "}
                Confirm Order
              </button>
              <button
                className="btn btn-block mb-2 checkOutbtn"
                onClick={() => {
                  value.clearCart();
                }}
              >
                Clear The Cart
              </button>
              <a href="">Please Select Delivery Zone</a>
            </div>
          </div>
        )}
      </ProductConsumer>

      {/* <!---checkOut-->  */}
    </div>
  );
}

export default ConfirmOrderCart;
