import React, { Component } from "react";
import "./css/form.css";
import Form from "./Form";
import joi from "joi-browser";
import Flip from "react-reveal/Flip";
import http from "../utils/httpService";
import { addProducts } from "../utils/apiEndpoint";
import ConfirmOrderModal from "./ConfirmOrderModal";
import { ProductConsumer } from "../context/context";
class CheckOutForm extends Form {
  schema = {
    firstName: joi.string().required().label("First Name"),
    lastName: joi.string().required().label("Last Name"),
    email: joi.string().email().required().label("Email"),
    mobileNumber: joi.number().required().label("Mobile Number"),
    address: joi.string().required().label("Address"),
  };

  doSubmit = async () => {
    const { appData } = this.props;

    const cart = [];
    appData.cart.forEach((item) => {
      cart.push({
        item_id: item.item_id,
        item_price: parseInt(item.price),
        item_quantity: item.count,
        item_instruction: "deliver warm",
      });
    });

    const obj = JSON.stringify({ order_details: cart });

    const compeleteOrder = {
      delivery_zone_id: appData.deliveryZone.delivery_zone_id,
      coupon_id:
        Object.keys(appData.coupon).length === 0 ? 0 : appData.coupon.coupon_id,
      first_name: this.state.data.firstName,
      last_name: this.state.data.lastName,
      email: this.state.data.email,
      address: this.state.data.address,
      contact_number: this.state.data.mobileNumber,
      order_instruction: this.state.instruction,
      grand_total: parseInt(appData.cartTotal),
      coupon_discount:
        Object.keys(appData.coupon).length === 0
          ? 0
          : appData.coupon.discount_in_percentage,
      delivery_charges: appData.deliveryZone.delivery_charges,
      order_status: "pending",
      meta_data: JSON.stringify({ order_details: cart }),
      shop_id: appData.shopId,
    };
    console.log(compeleteOrder);
    const response = await http.post(addProducts, compeleteOrder);
    if (response.status === 200) {
      this.setState({ confirmOrderMessage: true }, () => {
        console.log(this.state.confirmOrderMessage);
      });
    }
  };

  render() {
    const { errors, data } = this.state;
    const closeModal = () => {
      if (this.state.confirmOrderMessage === true) {
        this.setState({ confirmOrderMessage: false });
      }
      console.log("john cena");
    };
    return (
      <div className=" formContainer">
        <ProductConsumer>
          {(value) => {
            return (
              <ConfirmOrderModal
                confirmOrderMessage={this.state.confirmOrderMessage}
                closeModal={closeModal}
                clearCart={value.clearCart}
              />
            );
          }}
        </ProductConsumer>
        <form onSubmit={this.handleSubmit} id="checkoutForm">
          <h1 className="pt-5 mb-5">Checkout form</h1>
          <div className="form-group">
            <label>First Name</label>
            <input
              type="text"
              className={
                errors.firstName ? "form-control active" : "form-control"
              }
              id="Fname"
              placeholder="Please enter First Name"
              name="firstName"
              value={this.state.data.firstName}
              onChange={this.handleChange}
            />
            {errors.firstName && (
              <Flip top>
                {" "}
                <div className="errorDisp">{errors.firstName}</div>
              </Flip>
            )}
          </div>

          <div className="form-group">
            <label>Last Name</label>
            <input
              type="text"
              className={
                errors.lastName ? "form-control active" : "form-control"
              }
              id="Lname"
              placeholder="Please Enter Last Name"
              name="lastName"
              value={this.state.data.lastName}
              onChange={this.handleChange}
            />
            {errors.lastName && (
              <Flip top>
                {" "}
                <div className="errorDisp">{errors.lastName}</div>
              </Flip>
            )}
          </div>

          <div className="form-group">
            <label>Email address</label>
            <input
              type="email"
              className={errors.email ? "form-control active" : "form-control"}
              id="EmailAddress"
              placeholder="name@example.com"
              name="email"
              value={this.state.data.email}
              onChange={this.handleChange}
            />
            {errors.email && (
              <Flip top>
                {" "}
                <div className="errorDisp">{errors.email}</div>
              </Flip>
            )}
          </div>

          <div className="form-group">
            <label>Mobile Number</label>
            <input
              type="tel"
              className={
                errors.mobileNumber ? "form-control active" : "form-control"
              }
              id="phoneNumber"
              placeholder="03xxxxxxxxx"
              name="mobileNumber"
              value={this.state.data.mobileNumber}
              onChange={this.handleChange}
            />
            {errors.mobileNumber && (
              <Flip top>
                {" "}
                <div className="errorDisp">{errors.mobileNumber}</div>
              </Flip>
            )}
          </div>

          <div className="form-group">
            <label>Address</label>
            <input
              type="text"
              className={
                errors.address ? "form-control active" : "form-control"
              }
              id="Address"
              placeholder="Please Enter Adress"
              name="address"
              value={this.state.data.address}
              onChange={this.handleChange}
            />
            {errors.address && (
              <Flip top>
                {" "}
                <div className="errorDisp">{errors.address}</div>
              </Flip>
            )}
          </div>

          <div className="form-group textarea">
            <label>Order Instructions / Note to rider</label>
            <textarea
              className="form-control"
              id="textArea"
              rows="3"
              placeholder="Extr Note"
              name="instruction"
              value={this.state.instruction}
              onChange={this.handleChangeInstruction}
            ></textarea>
          </div>
        </form>
      </div>
    );
  }
}

export default CheckOutForm;
