import React from "react";

import "./css/checkoutProduct.css";
import LightSpeed from "react-reveal/LightSpeed";

function CheckoutProduct({ item, value }) {
  const { item_title, price, item_id, total, count, id } = item;
  const { decrement, increment, removeItem } = value;
  return (
    <div className="checkoutProduct">
      <LightSpeed right>
        <button
          className=""
          onClick={() => {
            removeItem(item_id);
            console.log("close");
          }}
        >
          <i className="fa fa-times" aria-hidden="true"></i>
        </button>
        <div className="checkoutProduct__title">
          <h5>{item_title}</h5>
        </div>
        <div className="checkoutProduct__detail">
          <div className="price">Rs.{total}</div>
          <div className="quantity">
            <button onClick={() => decrement(item_id)}>
              <i className="fa fa-minus " aria-hidden="true"></i>
            </button>
            <span>{count}</span>
            <button onClick={() => increment(item_id)}>
              <i className="fa  fa-plus" aria-hidden="true"></i>
            </button>
          </div>
        </div>
      </LightSpeed>
    </div>
  );
}

export default CheckoutProduct;
