import React, { Component } from "react";
import { getProducts, getDeliveryZone } from "../utils/apiEndpoint";

import http from "../utils/httpService";

const ProductContext = React.createContext();
class ProductProvider extends Component {
  state = {
    products: [],
    cart: [],
    cartSubtotal: 0,
    cartTax: 0,
    cartTotal: 0,
    deliveryCharge: 300,
    modalOpen: true,
    deliveryZone: "",
    deliveryZones: [],
    coupon: {},
    shopId: 1,
  };
  async componentDidMount() {
    this.setProdcuts();

    const { data } = await http.get(getDeliveryZone);
    this.setState({ deliveryZones: data.delivery_zones });
  }

  setDeliveryZone = (deliveryZone) => {
    // console.log(deliveryZone);
    const tempDz = this.state.deliveryZones;
    let item = tempDz.find((i) => {
      return i.delivery_zone_id === parseInt(deliveryZone);
    });
    const dZ = tempDz.indexOf(item);
    let selectedDeliveryZone = tempDz[dZ];
    this.setState({ deliveryZone: selectedDeliveryZone }, () => {
      console.log(this.state.deliveryZone);
    });
  };

  setCoupon = (couponData) => {
    this.setState({ coupon: couponData }, () => {
      console.log(this.state.coupon);
    });
  };
  setProdcuts = async () => {
    const { data } = await http.get(getProducts);

    const itemSection = data.item_details;

    let tempProducts = [];
    itemSection?.forEach((item) => {
      const singleItem = { ...item };
      singleItem.products.forEach((n) => {
        n["inCart"] = false;
        n["count"] = 0;
        n["total"] = 0;
      });
      tempProducts = [...tempProducts, singleItem];
    });
    this.setState(() => {
      return { products: tempProducts };
    });
  };

  getItem = (id) => {
    const product = this.state.products.find((p) => {
      console.log(p);
    });
    return product;
  };
  removeItem = (id) => {
    let categories = [...this.state.products];
    let tempCart = [...this.state.cart];
    tempCart = tempCart.filter((item) => item.item_id !== id);

    let tempProducts = [];
    categories?.forEach((item) => {
      const singleItem = { ...item };
      singleItem.products.forEach((n) => {
        if (n.item_id === id) {
          n.inCart = false;
          n.count = 0;
          n.total = 0;
        }
      });

      tempProducts = [...tempProducts, singleItem];
    });
    this.setState(
      () => {
        return {
          cart: [...tempCart],
          products: [...tempProducts],
        };
      },
      () => {
        this.addTotals();
        console.log(this.state.cart);
      }
    );

    // const index = tempProducts.indexOf(this.getItem(id));
    // let removedProduct = tempProducts[index];
    // removedProduct.inCart = false;
    // removedProduct.count = 0;
    // removedProduct.total = 0;
  };
  addToCart = (id) => {
    let categories = [...this.state.products];
    let product;
    let tempProducts = [];
    categories?.forEach((item) => {
      const singleItem = { ...item };
      singleItem.products.forEach((n) => {
        if (n.item_id === id) {
          n.inCart = true;
          n.count = 1;
          const price = n.price;
          n.total = price;
          product = n;
        }
      });

      tempProducts = [...tempProducts, singleItem];
    });
    this.setState(
      {
        products: tempProducts,
        cart: [...this.state.cart, product],
      },
      () => {
        this.addTotals();
        console.log(this.state.cart);
      }
    );

    // const product = tempProducts[index];
    // product.inCart = true;
    // product.count = 1;
    // const price = product.price;
    // product.total = price;
    // this.setState(
    //   () => {
    //     return { products: tempProducts, cart: [...this.state.cart, product] };
    //   },
    //   () => {
    //     this.addTotals();
    //     console.log(this.state.cart);
    //   }
    // );
  };

  increment = (id) => {
    let tempCart = [...this.state.cart];
    const selectedProduct = tempCart.find((item) => item.item_id === id);

    const index = tempCart.indexOf(selectedProduct);
    const product = tempCart[index];
    product.count = product.count + 1;
    product.total = product.count * product.price;
    this.setState(
      () => {
        return {
          cart: [...tempCart],
        };
      },
      () => {
        this.addTotals();
      }
    );
  };

  decrement = (id) => {
    let tempCart = [...this.state.cart];
    const selectedProduct = tempCart.find((item) => item.item_id === id);

    const index = tempCart.indexOf(selectedProduct);
    const product = tempCart[index];
    product.count = product.count - 1;
    if (product.count === 0) {
      this.removeItem(id);
    } else {
      product.total = product.count * product.price;
      this.setState(
        () => {
          return {
            cart: [...tempCart],
          };
        },
        () => {
          this.addTotals();
        }
      );
    }
  };

  clearCart = () => {
    this.setState(
      () => {
        return { cart: [] };
      },
      () => {
        this.setProdcuts();
        this.addTotals();
      }
    );
  };
  addTotals = () => {
    let subTotal = 0;
    this.state.cart.map((item) => (subTotal += parseFloat(item.total)));
    const tempTax = subTotal * 0.16;
    const tax = parseFloat(tempTax.toFixed(2));
    const total = subTotal + tax + this.state.deliveryCharge;
    const couponTotal =
      total * (parseInt(this.state.coupon.discount_in_percentage) / 100);
    const discountedTotal = total - couponTotal;

    this.setState(() => {
      return {
        cartSubtotal: subTotal,
        cartTax: tax,
        cartTotal: total.toFixed(2),
        discountedTotal,
      };
    });
  };
  render() {
    return (
      <ProductContext.Provider
        value={{
          ...this.state,
          addToCart: this.addToCart,
          increment: this.increment,
          decrement: this.decrement,
          removeItem: this.removeItem,
          clearCart: this.clearCart,
          setDeliveryZone: this.setDeliveryZone,
          setCoupon: this.setCoupon,
          addTotals: this.addTotals,
        }}
      >
        {this.props.children}
      </ProductContext.Provider>
    );
  }
}

const ProductConsumer = ProductContext.Consumer;
export { ProductProvider, ProductConsumer };
