import Product from "./Product";
import "./css/ProductPage.css";

import Scrollbar from "./Scrollbar";

import Footer from "./Footer";
import { scrollEffect } from "./../utils/scroll";

import MyModal from "./Modal";

import React, { Component } from "react";
import { ProductConsumer } from "../context/context";
class ProductPage extends Component {
  componentDidMount() {
    scrollEffect();
  }

  render() {
    return (
      <div className=" px-0 contentContainer">
        <Scrollbar />

        <ProductConsumer>
          {(value) => {
            return value.products?.map((item) => {
              return (
                <section
                  key={item.category_id}
                  id={item.category_name}
                  className="mt-5"
                >
                  <div className="container-fluid px-0 px-md-3 px-lg-5 cardRow mt-5 pb-4 ">
                    <div className="sectionHeading mb-4">
                      <h1 className="py-3 text-center border-bottom ">
                        {item.category_name}
                      </h1>
                    </div>

                    <div className="row mb-4 justify-content-center  mx-0">
                      {item.products.map((product) => {
                        return (
                          <Product product={product} key={product.item_id} />
                        );
                      })}
                    </div>
                  </div>
                </section>
              );
            });
          }}
        </ProductConsumer>

        <Footer />
      </div>
    );
  }
}

export default ProductPage;
