import React, { useState } from "react";
import "./css/confirmOrderModal.css";
import { Modal } from "react-bootstrap";
function ConfirmOrderModal({ confirmOrderMessage, closeModal, clearCart }) {
  const [show, setShow] = useState(confirmOrderMessage);

  const handleClose = () => {
    clearCart();
    closeModal();
  };
  const handleShow = () => {
    setShow(true);

    console.log(confirmOrderMessage);
  };

  return (
    <>
      <Modal show={confirmOrderMessage} onHide={() => handleClose()}>
        <Modal.Header closeButton>
          <Modal.Title>Order Confirmation</Modal.Title>
        </Modal.Header>
        <Modal.Body className="confirmModal">
          <div className="confirmOrderDiv">
            <div className="confirmContent">
              <div>
                <h4>Your Order Is Placed Successfully</h4>
              </div>
            </div>

            <button
              className="btn btn-danger w-100"
              variant="primary"
              onClick={() => {
                handleClose();
              }}
            >
              Ok
            </button>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
}

export default ConfirmOrderModal;
